import { Component, NgZone, OnInit } from '@angular/core';
import { ApiservicesService } from '../../provider/apiservices.service';
import { Router } from "@angular/router";
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';
import { MenuController } from '@ionic/angular';
import { DataTransferService } from '../../services/data-transfer.service';
import { LocalStorageService } from '../../services/local-storage.service';

@Component({
  selector: 'app-hr-policy-list',
  templateUrl: './hr-policy-list.page.html',
  styleUrls: ['./hr-policy-list.page.scss'],
})

export class HrPolicyListPage implements OnInit{

  hrPolicyList:any;
  errorMessage:string="";
  myInfiniteScroll:any;
  myinfiniteScrollCall:boolean=false;
  value:any="0";
  hrPolicyListData:any;
  showErrorImage:any="";
  no_data_message:any="";
  pageHeader:any;
  hrCategoryData: any;

  constructor(private apiservices:ApiservicesService,private router:Router,private zone:NgZone,public menuCtrl:MenuController,
    private sendData:DataTransferService,private storageService:LocalStorageService) {
      this.hrCategoryData=this.sendData.alldata;


  }









  ionViewWillEnter(){
    this.apiservices.hrPolicyCount=0;
    this.storageService.getStorage().then(res=>{
      this.value="0";
      this.hrPolicyFunc();
      console.log("ionic lifecycle called getStorage==>",res);
    });
  }

  ngOnInit(){

  }

  hrPolicyFunc(){
    this.zone.run(()=>{
      console.log("HrPolicy List api call function");
      let apikey={
        "limit":this.value,
        "category_id":this.hrCategoryData.category_id
      }
      this.apiservices.apiCallWithLoginToken(URLS.hrPolicyList, apikey).subscribe((result)=>{
        console.log("HrPolicy List api result==", result);
        this.pageHeader=result.data.settings.page_header;
        if(result.status==1 || result.status=='1'){
          this.hrPolicyListData=result.data;
          if(this.hrPolicyList==undefined){
            this.hrPolicyList=this.hrPolicyListData.hrpolicy;
            console.log("HrPolicy List Infinite Scroll if1==", this.hrPolicyList);
          }else{
            this.hrPolicyList=this.hrPolicyList.concat(this.hrPolicyListData.hrpolicy);
            console.log("HrPolicy List Infinite Scroll else1==", this.hrPolicyList);
          }
          if(this.myinfiniteScrollCall==true){
            console.log("HrPolicy List Infinite Scroll if2==", this.hrPolicyList);
            this.myInfiniteScroll.target.complete();
            this.errorMessage = "";
            this.myinfiniteScrollCall=false;
          }
        }else{
          this.errorMessage = result.message;
          this.no_data_message=result.data.settings.no_data_msg;
          this.showErrorImage=result.data.settings.no_data_image;
          console.log("HrPolicy List Infinite Scroll12==", this.hrPolicyList);
          if(this.myinfiniteScrollCall==true){
            console.log("HrPolicy List Infinite Scroll123==", this.hrPolicyList);
            this.myInfiniteScroll.target.complete();
            this.myinfiniteScrollCall=false;
          }
        }
      }, err =>{
        this.apiservices.showToastMessage(JSON.stringify(err),'top',3000,'redBg');
      })
    });
  }

  doInfinite(infiniteScroll){
    this.myinfiniteScrollCall=true;
    console.log("Begin async operation 1==");
    setTimeout(() => {
      this.value=this.hrPolicyList.length;
      console.log("Begin async operation 2==",this.value);
      this.myInfiniteScroll = infiniteScroll;
      this.hrPolicyFunc();
    }, 500);
  }

  goToAcknow(policyData){
    this.zone.run(()=>{ 
      this.sendData.alldata=policyData;
      let data={
        "commingFrom":"hrPolicyList"
      }
      this.sendData.commingPageName=data;
      this.router.navigate(["/hr-policy-detail"]);
    })
  }









}